@import url("https://fonts.googleapis.com/css?family=Assistant&display=swap");
* body {
  font-family: "Merriweather", serif; }
  * body .title, * body--title {
    font-family: "Montserrat", sans-serif; }

.body {
  margin-top: 120px;
  margin-left: auto;
  margin-right: auto;
  width: 70%; }

.my-container {
  max-width: 1180px;
  margin: 0px auto; }

#headline-title {
  border-bottom: 2px solid #58b1e1;
  padding-bottom: 3px;
  display: flex;
  justify-content: space-between;
  font-size: 1.5em; }

#headline-title .title {
  background-color: #58b1e1;
  color: #fff;
  text-transform: uppercase;
  font-weight: bold;
  padding-left: 20px;
  padding-right: 100px;
  border-top-right-radius: 15px; }

#headline-title .infor {
  border-radius: 10px;
  border: 2px solid #58b1e1;
  color: #58b1e1;
  background-color: #fff; }

#headline-title .infor * {
  margin: auto 10px; }
