@import url("https://fonts.googleapis.com/css?family=Assistant&display=swap");
* body {
  font-family: "Merriweather", serif; }
  * body .title, * body--title {
    font-family: "Montserrat", sans-serif; }

.body {
  margin-top: 120px;
  margin-left: auto;
  margin-right: auto;
  width: 70%; }

.my-container {
  max-width: 1180px;
  margin: 0px auto; }

.footer-wrapper {
  font-size: 16px;
  background-color: #FCFCFC; }
  .footer-wrapper__divider {
    display: block;
    height: 1px;
    border: 0;
    border-top: 1px solid #EDF7FC;
    margin: 0;
    padding: 0; }
  .footer-wrapper__second-floor {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70px; }
    .footer-wrapper__second-floor__item {
      text-decoration: none;
      color: #58b1e1;
      padding: 20px 40px;
      text-transform: uppercase;
      font-family: "Montserrat", sans-serif;
      font-size: 0.75em;
      letter-spacing: .14em; }
