.headline {
  display: flex;
  border-bottom: solid 4px #58b1e1;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  justify-content: space-between; }
  .headline .headline-title {
    height: 24px;
    width: 200px;
    text-align: center;
    color: #fff;
    font-weight: 600;
    background-color: #58b1e1;
    padding: 7px 20px 7px 0px;
    border-top-right-radius: 5px; }
  .headline .headline-switch {
    display: flex;
    padding: 4px 0;
    background-color: #58b1e1;
    color: #fff;
    font-weight: 600;
    border-bottom: none;
    text-align: center; }
    .headline .headline-switch > div {
      width: 22px;
      cursor: pointer;
      font-weight: 600; }
    .headline .headline-switch > div:hover {
      color: #a09e9e; }
    .headline .headline-switch #prev {
      padding-left: 7px; }
    .headline .headline-switch #next {
      padding-right: 7px; }

@media only screen and (max-width: 768px) {
  .headline {
    border-bottom: solid 4px #58b1e1;
    margin-left: auto;
    margin-right: auto;
    width: 100%; }
    .headline .headline-title {
      height: 35px;
      width: 200px;
      text-align: center;
      color: #fff;
      font-weight: 600;
      background-color: #58b1e1;
      padding: 7px 0; } }
