@import url("https://fonts.googleapis.com/css?family=Assistant&display=swap");
* body {
  font-family: "Merriweather", serif; }
  * body .title, * body--title {
    font-family: "Montserrat", sans-serif; }

.body {
  margin-top: 120px;
  margin-left: auto;
  margin-right: auto;
  width: 70%; }

.my-container {
  max-width: 1180px;
  margin: 0px auto; }

.header-wrapper {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  border-bottom: 1px solid #EDF7FC;
  background-color: #FCFCFC; }
  .header-wrapper__page-menu-area {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 70px;
    margin: 0 70px; }
    .header-wrapper__page-menu-area__left {
      display: flex;
      align-items: center;
      height: 100%;
      font-weight: 400;
      font-size: 0.8em; }
      .header-wrapper__page-menu-area__left__logo-wrapper__a__img {
        height: 70px; }
      .header-wrapper__page-menu-area__left__navbar {
        height: 100%; }
        .header-wrapper__page-menu-area__left__navbar__list-items {
          display: flex;
          height: 100%;
          margin: 0;
          padding-left: 20px;
          list-style-type: none; }
          .header-wrapper__page-menu-area__left__navbar__list-items__item {
            height: 100%;
            text-transform: uppercase; }
            .header-wrapper__page-menu-area__left__navbar__list-items__item:hover .category-name {
              color: #58b1e1; }
            .header-wrapper__page-menu-area__left__navbar__list-items__item:hover .hover-dropdown {
              opacity: 1 !important;
              visibility: visible !important;
              overflow: visible !important; }
            .header-wrapper__page-menu-area__left__navbar__list-items__item .category-name {
              padding: 0 20px;
              text-decoration: none;
              color: #363738;
              height: 100%;
              display: flex;
              justify-content: center;
              align-items: center; }
            .header-wrapper__page-menu-area__left__navbar__list-items__item .hover-dropdown {
              opacity: 0;
              visibility: hidden;
              overflow: hidden; }
            .header-wrapper__page-menu-area__left__navbar__list-items__item__dropdown {
              position: absolute;
              margin: 0;
              padding: 0;
              list-style-type: none;
              background: #fbfbfb; }
              .header-wrapper__page-menu-area__left__navbar__list-items__item__dropdown__inner {
                position: relative;
                z-index: 200;
                display: block;
                padding: 0;
                margin: 0;
                z-index: 997;
                border: 1px solid #EDF7FC; }
                .header-wrapper__page-menu-area__left__navbar__list-items__item__dropdown__inner__list-items {
                  margin: 0;
                  padding: 20px 0 20px 0;
                  list-style-type: none;
                  display: flex;
                  flex-direction: column;
                  align-items: center; }
                  .header-wrapper__page-menu-area__left__navbar__list-items__item__dropdown__inner__list-items__item {
                    width: 248px;
                    height: 33px; }
                    .header-wrapper__page-menu-area__left__navbar__list-items__item__dropdown__inner__list-items__item .sub-category-name {
                      padding: 0 20px;
                      text-decoration: none;
                      color: #363738; }
    .header-wrapper__page-menu-area__right__more-btn {
      font-size: 0.5em;
      letter-spacing: 2px; }
